<template>
  <div id="courseList">
    <div v-if="course.length>0">
      <div class="item" :key="i" v-for="(item ,i) in course">
        <div class="tt-btn">
          <div class="tt">{{item.CourseName}}</div>
          <div class="btn">
            <button
              v-if="courseType==='current' "
              :class=" (item.State === 1 ? 'btn-able' : 'btn-disable') "
              :disabled="item.State !== 1"
              @click="selectCourse(item)"
            >{{item.State === 1 ? '可进入' : '未开始'}}</button>
            <button v-else-if="courseType!=='current'" @click="selectCourse(item)">查看</button>
            <!-- && item.State===3 -->
          </div>
        </div>
        <div class="tc-gd">
          <div class="tc">老师：{{item.TeacherName}}</div>
          <div class="gd">班级：{{item.ClassName}}</div>
        </div>
        <div class="cs-tm">
          <div class="cs">教室：{{item.ClassroomName}}</div>
          <div class="tm">
            时间：
            {{item['ClassDate'] | formatDay(item['ClassDate']) }}
            {{item['StartTime'] | formatTime(item['StartTime']) }}
            -
            {{item['EndTime'] | formatTime(item['EndTime']) }}
          </div>
        </div>
        <div class="line"></div>
      </div>
    </div>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>
// import { formatDate } from "@/utils/formatData";

export default {
  name: "",
  components: {},
  props: {
    courseType: {
      type: String,
      default: "current"
    },
    course: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      sites: [1, 2, 3]
    };
  },
  created() {},
  methods: {
    selectCourse(item) {
      this.$store.commit("SET_COURSE", item);
      this.$store.commit("SET_COURSEID", item.CourseID);

      if (this.courseType === "current") {
        this.$store
          .dispatch("GetCourseServiceByUser", item.ClassroomID)
          .then(res => {
            if (res.Result) {
              this.$router.push("/course");       
            } else if (res.Info) {
              this.$toast(res.Info);
            } else {
              this.$toast("获取教室服务失败");
            }
          })
          .catch(err => {
            this.$toast("获取教室服务出错");
          });
      } else {
        this.$router.push("/history");
      }
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less'>
#courseList {
  .item {
    padding: 1.5rem;
    width: calc(100vw - 3rem);
    text-align: left;
    color: #666;
    font-size: 1.2rem;
    position: relative;
    .tt-btn {
      margin-bottom: 4vw;
      overflow: hidden;
      .tt {
        float: left;
        color: #333;
        font-size: 1.6rem;
      }
      .btn {
        button {
          float: right;
          width: 14.3vw;
          height: 5.3vw;
          background: rgba(0, 203, 171, 1);
          border-radius: 3px;
          color: #fff;
          font-size: 1rem;
          line-height: 5.3vw;
        }
        .btn-able {
          background: rgba(0, 203, 171, 1);
        }
        .btn-disable {
          background: rgb(255, 112, 83);
        }
      }
    }
    .tc-gd,
    .cs-tm {
      display: flex;
      // justify-content:space-between;
      line-height: 2;
      .tc,
      .cs {
        width: 40%;
      }
      .gd,
      .tm {
        width: 60%;
      }
    }
    .line {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 92vw;
      height: 1px;
      background: rgba(221, 221, 221, 1);
      right: 0;
      margin-left: auto;
      margin-right: auto;
    }
    &:last-child {
      .line {
        display: none;
      }
    }
  }
}
</style>


