<template>
  <div id="home-course">
    <head-top title="课程" :show-back="false">
      <!-- <span v-show="headTopActive !==0" @click="goBack" class="head-back" slot="left">
        <i class="iconfont iconrili"></i>
      </span>-->
    </head-top>
    <van-tabs v-model="headTopActive" @change="changeCourse">
      <van-tab title="当前课程">
        <van-pull-refresh v-model="isLoading" @refresh="changeCourse" class="pull_fresh">
          <!-- <router-link to="/course"> -->
          <course-list class="list_card" course-type="current" :course="currentCourse.data"></course-list>
          <!-- </router-link> -->
        </van-pull-refresh>
      </van-tab>
      <van-tab title="历史课程">
        <!-- <van-field
          class="course_date"
          left-icon="tosend"
          type="date"
          v-model="historyCourse.form.startDate"
          @change="startChange()"
          :max="nowDate"
          placeholder="开始日期"
        >
        </van-field>至
        <van-field
          class="course_date"
          left-icon="tosend"
          type="date"
          v-model="historyCourse.form.endDate"
          @change="endChange()"
          :max="nowDate"
          placeholder="结束日期"
        >
        </van-field>-->
        <van-pull-refresh v-model="isLoading" @refresh="changeCourse" class="pull_fresh">
          <!-- <router-link to="/history"> -->
          <course-list class="list_card" course-type="history" :course="historyCourse.data"></course-list>
          <!-- </router-link> -->
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import courseList from "@/components/courseList";
import { Tab, Tabs, Field } from "vant";
// import { apiGetStudentCourseList } from "@/api/main/course";
import { getNowDate } from "@/utils/index";
export default {
  name: "",
  components: {
    headTop,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Field.name]: Field,
    courseList
  },
  props: {},
  data() {
    return {
      headTopActive: 0,
      nowDate: getNowDate(),
      currentCourse: {
        form: {
          startDate: getNowDate(),
          endDate: getNowDate(),
          stuid: this.$store.getters.studentID || "",
          psize: 10,
          pno: 1,
          estate: 1
        },
        data: []
      },
      historyCourse: {
        form: {
          startDate: "",
          endDate: getNowDate(),
          stuid: this.$store.getters.studentID || "",
          psize: 10,
          pno: 1,
          estate: 2
        },
        data: []
      },
      isLoading: false
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (from.path.indexOf("/history") > -1) {
        vm.headTopActive = 1;
      }
    });
  },
  mounted() {
    this.$nextTick(() => {
      let start = new Date();
      // start.setDate(start.getDate() - 7);
      start.setDate(start.getDate() - 30);
      let Y = start.getFullYear();
      let M =
        start.getMonth() + 1 < 10
          ? "0" + (start.getMonth() + 1)
          : start.getMonth() + 1;
      let D = start.getDate() < 10 ? "0" + start.getDate() : start.getDate();
      this.historyCourse.form.startDate = Y + "-" + M + "-" + D;
      this.changeCourse();
    });
  },
  methods: {
    goBack() {
      console.log("返回监听");
    },
    currentStudentCourse() {
      this.currentCourse.data = [];
      this.$mainHttp.course
        .getStudentCourseList(this.currentCourse.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.currentCourse.data.push(item);
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    historyStudentCourse() {
      this.historyCourse.data = [];
      this.$mainHttp.course
        .getStudentCourseList(this.historyCourse.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              if (
                !this.historyCourse.data.find(
                  r => JSON.stringify(r) === JSON.stringify(item)
                )
              )
                this.historyCourse.data.push(item);
            });
          } else {
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    changeCourse() {
      this.headTopActive === 0
        ? this.currentStudentCourse()
        : this.historyStudentCourse();
    },
    startChange(e) {
      let start = new Date(this.historyCourse.form.startDate).getTime();
      let end = new Date(this.historyCourse.form.endDate).getTime();
      if (start > end) {
        this.historyCourse.form.startDate = this.historyCourse.form.endDate;
      }
      this.historyStudentCourse();
    },
    endChange(e) {
      let start = new Date(this.historyCourse.form.startDate).getTime();
      let end = new Date(this.historyCourse.form.endDate).getTime();
      if (start > end) {
        this.historyCourse.form.endDate = this.historyCourse.form.startDate;
      }
      this.historyStudentCourse();
    }
  },
  computed: {},
  watch: {},
  created() {}
};
</script>

<style lang='less'>
#home-course {
  .head-back {
    .icon-fanhui {
      display: none;
    }
  }
  .rightBtn {
    button {
      position: absolute;
      right: 4vw;
      top: 4vw;
      font-size: 1.2rem;
      width: 13vw;
      height: 5.3vw;
      background: rgba(0, 203, 171, 1);
      line-height: 1.5rem;
      color: #fff;
    }
  }

  .van-tabs__wrap {
    // height: 5rem;
    .van-tab {
      // line-height: 5rem;
      span {
        font-size: 1.17rem;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(0, 203, 171, 1);
      }
    }
    .van-tabs__line {
      width: 16vw;
      height: 1px;
      background: rgba(0, 203, 171, 1);
    }
    // .van-tabs__content{

    // }
  }

  .pull_fresh {
    .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 50px - 44px - 15px);
    }
  }

  .course_date {
    width: calc(45vw);
    margin: 0 calc(1vw);
    margin-top: 10px;
    display: inline-flex;
  }

  .list_card {
    max-height: calc(100vh - 4.3rem - 44px - 6vh - 2rem);
    margin-top: 1rem;
    overflow-y: auto;
    background: #fff;
  }
}
</style>


